<template>
    <section id="museum" class="museum">
        <h2 class="museum__heading heading">музеи</h2>
        <yandex-map class="museum__map" :coords="getCoords" zoom="15" :scrollZoom="false"
                    :cluster-options="{ 1: { clusterDisableClickZoom: true } }"
                    :restrictMapArea='[[59.838,29.511], [60.056,30.829]]' :controls="[]">
            <ymap-marker v-for="marker in contents" :markerId="marker.id" marker-type="placemark"
                         :coords="marker.coords" :hint-content="marker.name"
                         :balloon="{ header: marker.name, body: marker.address, footer: marker.tel }"
                         iconLayout="default#image" :icon="markerIcon"></ymap-marker>
        </yandex-map>
        <div class="museum__wrapper container">
            <div class="museum__item" v-for="museum in contents" :key="museum.id">
                <a class="museum__name" @click.prevent="currentCoords = museum.coords">{{ museum.name }}</a>
                <p class="museum__address">{{ museum.address }}<br>{{ museum.tel }}</p>
            </div>
        </div>
    </section>
</template>

<script>
    import {yandexMap, ymapMarker} from "vue-yandex-maps";

    export default {
        name: "Museums",
        components: {yandexMap, ymapMarker},
        data() {
            return {
                markerIcon: {
                    layout: 'default#imageWithContent',
                    imageHref: './img/Vector.svg',
                    imageSize: [30, 40],
                    imageOffset: [-15, -40],
                    contentOffset: [0, 15],
                },
                currentCoords: [59.938782, 30.332383],
                contents: [
                    {
                        id: 1,
                        name: 'Русский музей',
                        address: '191186, Санкт-Петербург, Инженерная ул., 4,',
                        tel: '+7 (812) 595-42-40',
                        coords: [59.938782, 30.332383]
                    },
                    {
                        id: 2,
                        name: 'Третьяковская галерея',
                        address: '119017, Москва, Лаврушинский переулок,10',
                        tel: '+7 (495) 957-07-27',
                        coords: [55.741392, 37.620521]
                    },
                    {
                        id: 3,
                        name: 'Мультимедиа Арт Музей',
                        address: '105064, Москва, Гороховский переулок, 7',
                        tel: '+7 (495) 632–16–81',
                        coords: [55.765150, 37.664842]
                    },
                ]
            }
        },
        computed: {
            getCoords() {
                return this.currentCoords
            }
        }

    }
</script>

<style lang="scss">
    .museum {
        padding: 60px 0 58px;
    }

    .museum__heading {
        text-align: center;
    }

    .museum__map {
        width: 320px;
        height: 514px;
        margin: 43px auto 0;
    }

    .museum__name {
        position: relative;
        font-size: 16px;
        line-height: 26px;
        font-weight: bold;
        cursor: pointer;
        margin: 0;
        padding-left: 29px;

        &::before {
            content: "";
            position: absolute;
            width: 14px;
            height: 18px;
            background-image: url("../../public/img/Vector.svg");
            left: 0;
            top: 4px;
        }
    }

    .museum__item {
        width: 320px;
        padding: 39px 24px;
        border-bottom: 1px solid #E5E5E5;
    }

    .museum__address {
        width: 200px;
        font-size: 14px;
        line-height: 24px;
        color: #BFBFBF;
        margin: 23px 0 0;
    }

    @media (min-width: 1024px) {
        .museum {
            padding: 0;
        }

        .museum__map {
            width: 1200px;
        }

        .museum__wrapper {
            display: flex;
            justify-content: center;
            padding-top: 40px;
            margin-bottom: 100px;
        }

        .museum__item {
            width: 320px;
            padding: 0 40px 40px;
            border-bottom: none;

            &:nth-child(2) {
                border-left: 1px solid #E5E5E5;
                border-right: 1px solid #E5E5E5;
            }

            &:first-of-type {
                width: 280px;
                padding-left: 0;
            }
        }

        .museum__heading {
            text-align: center;
            margin: 40px 0;
        }

        .museum__address {
            display: flex;
            justify-content: center;
            margin-top: 21px;
        }
    }
</style>
