<template>
    <div id="slider" class="slider">
        <div class="slider__pre-wrapper">
            <div class="slider__wrapper" ref="scroll" @scroll="onScroll">
                <div class="slider__post-wrapper">

                    <ul class="slider__dots" :style="{ 'transform': `translate3d(${currentPosition}px, 0, 0)` }"
                        ref="slidesContainer">
                        <li v-for="(src, index) in imgList" :key="src.imgNumber"
                            :style="{backgroundImage: `url(${ src.imgUrl })`}"
                            :class="{ active:index === active }" @click="changeSlide(index)">
                        </li>
                    </ul>
                    <router-link class="slider__router-link" to="/gallery">все картины</router-link>

                    <transition-group tag="div" :name="direction > 0 ? 'slide' : 'slide-invert'" class="slider__list">
                        <img v-for="(image, index) in imgList" :src="image.imgUrl" :key="image.imgNumber"
                             v-show="index === active" :alt="image.imageName">
                    </transition-group>
                    <button class="slider__prev" @click="changeSlide(active-1)" :disabled="active <= 0">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 41 12">
                            <defs/>
                            <path d="M.4697 5.4697a.75.75 0 000 1.0606l4.773 4.773a.75.75 0 101.0606-1.0607L2.0607 6l4.2426-4.2426A.75.75 0 105.2426.6967l-4.773 4.773zM41 5.25H1v1.5h40v-1.5z"/>
                        </svg>
                    </button>
                    <div class="slider__info" v-for="(name, index) in imgList" :key="name.imgNumber" v-show="index === active">
                        <p class="slider__name" >
                        {{ name.imageName }}</p>
                        <p class="slider__number">
                            <span class="slider__active">{{ name.imgNumber }}</span> / {{ total }}
                        </p>
                    </div>
                    <button class="slider__next" @click="changeSlide(active+1)" :disabled="active >= 4">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 41 12">
                            <defs/>
                            <path d="M40.5303 6.5303a.75.75 0 000-1.0606L35.7574.6967a.75.75 0 00-1.0607 1.0607L38.9393 6l-4.2426 4.2426a.75.75 0 001.0607 1.0607l4.7729-4.773zM0 6.75h40v-1.5H0v1.5z"/>
                        </svg>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Slider",
        data() {
            return {
                active: 0,
                direction: 1,
                currentIndex: 0,
                startX: null,
                isMounted: false,
                imgList: [
                    {
                        imgNumber: '1',
                        imgUrl: './img/boris-kustodiev-1.jpg',
                        imageName: 'Название'
                    },
                    {
                        imgNumber: '2',
                        imgUrl: './img/boris-kustodiev-2.jpg',
                        imageName: 'Название'
                    },
                    {
                        imgNumber: '3',
                        imgUrl: './img/boris-kustodiev-3.jpg',
                        imageName: 'Название'
                    },
                    {
                        imgNumber: '4',
                        imgUrl: './img/boris-kustodiev-4.jpg',
                        imageName: 'Название'
                    },
                    {
                        imgNumber: '5',
                        imgUrl: './img/boris-kustodiev-5.jpg',
                        imageName: 'Название'
                    }
                ]
            }
        },
        computed: {
            total() {
                return this.imgList.length;
            },
            currentPosition() {
                if (!this.isMounted) {
                    return 0;
                } else {
                    return -((this.$refs.scroll.offsetWidth - 100) * this.currentIndex);
                }
            }
        },
        mounted() {
            this.isMounted = true;

            this.$refs.slidesContainer.addEventListener('touchstart', event => {
                this.startX = (event.touches || event.originalEvent.touches)[0].clientX;
            });

            this.$refs.slidesContainer.addEventListener('touchmove', event => {
                if (!this.startX) return;
                const xDelta = this.startX - (event.touches || event.originalEvent.touches)[0].clientX;
                if (xDelta > 45) {
                    this.goNextSlide();
                    this.startX = null;
                } else if (xDelta < -45) {
                    this.goPreviousSlide();
                    this.startX = null;
                }
            });
        },
        methods: {
            changeSlide(index) {
                if (index >= 0 && index < this.imgList.length) {
                    this.direction = index > this.active ? 1 : -1;
                    this.active = index;
                }
            },
            onScroll() {
                if (this.$refs.scroll.scrollLeft <= 100) {
                    this.active = 0;
                }
                if (this.$refs.scroll.scrollLeft > 100) {
                    this.active = 1;
                }
                if (this.$refs.scroll.scrollLeft > 320) {
                    this.active = 2;
                }
                if (this.$refs.scroll.scrollLeft > 540) {
                    this.active = 3;
                }
                if (this.$refs.scroll.scrollLeft >= 760) {
                    this.active = 4;
                }
            },
            // Предыдущий слайд
            goPreviousSlide() {
                this.currentIndex -= 1;
                this.active = this.currentIndex;
                if (this.currentIndex < 0) {
                    this.currentIndex = this.imgList.length - 1;
                }
            },

            // Следующий слайд
            goNextSlide() {
                this.currentIndex += 1;
                this.active = this.currentIndex;
                if (this.currentIndex > this.imgList.length - 1) {
                    this.currentIndex = 0;
                }
            },
        },
    }
</script>

<style lang="scss">
    @media (max-width: 1024px) {
        .slider {
            display: flex;
            background-color: #202020;
        }

        .slider__pre-wrapper {
            position: relative;
            width: 320px;
            display: inline-block;
            vertical-align: top;
            overflow: hidden;
            margin: 0 auto;
        }

        .slider__wrapper {
            padding: 100px 0 152px;
            margin: 0 0 -20px;
            overflow-x: hidden;
        }

        .slider__post-wrapper {
            display: inline-block;
            vertical-align: top;
            font-size: 0;
            white-space: nowrap;
        }

        .slider__list {
            display: none;
        }

        .slider__prev,
        .slider__next {
            display: none;
        }

        .slider__prev {
            left: 0;
        }

        .slider__dots {
            list-style: none;
            padding: 0;
            margin: 0;
            width: 100%;
            text-align: left;
            transition: transform 0.5s ease;
        }

        .slider__dots > li {
            background-size: cover;
            width: 200px;
            height: 156px;
            margin: 0 20px 0 0;
            padding: 0;
            cursor: pointer;
            display: inline-block;
            vertical-align: top;

            &:first-of-type {
                margin-left: 60px;
            }

            &:last-of-type {
                margin-right: 60px;
            }
        }

        .slider__dots > .slider__router-link {
            display: flex;
            align-items: center;
            font-family: Helvetica, sans-serif;
            font-size: 14px;
            line-height: 20px;
            color: #F5F5F5;
        }

        .slider__router-link {
            position: absolute;
            bottom: 19px;
            left: 50%;
            transform: translateX(-50%);
            font-size: 14px;
            font-weight: 200;
            line-height: 20px;
            letter-spacing: 0.06em;
            color: #BFBFBF;
            padding-right: 15px;

            &::before {
                content: "";
                position: absolute;
                width: 5px;
                height: 5px;
                border-right: 1px solid #BFBFBF;
                border-bottom: 1px solid #BFBFBF;
                top: 50%;
                right: 6px;
                transform: rotate(-45deg);
                margin-top: -1px;
            }
        }

        .slider__info {
            position: absolute;
            width: 100%;
            text-align: center;
            font-size: 14px;
            font-weight: 200;
            margin: 0;
            left: 0;
        }

        .slider__name {
            color: #F5F5F5;
            top: 56px;
            margin: 0;
        }

        .slider__number {
            color: #BFBFBF;
            bottom: 91px;
            margin: 0;
        }

        .slider__active {
            color: #F5F5F5;
        }
    }

    @media (min-width: 1024px) {
        .slider {
            display: block;
            background-color: #202020;
        }

        .slider__wrapper {
            position: relative;
            user-select: none;
            width: 920px;
            padding: 40px 0 35px;
            margin: 0 auto;
        }

        .slider__post-wrapper {
            display: flex;
            flex-wrap: wrap;
        }

        .slider button {
            cursor: pointer;
            border: none;
            outline: none;
            background-color: transparent;

            > svg {
                fill: #BFBFBF;
            }
        }

        .slider__list {
            position: relative;
            display: block;
            overflow: hidden;
            margin: 0;
            width: 100%;
            height: 530px;
            padding: 50% 0 0;
            box-sizing: border-box;
        }

        .slider__list > img {
            position: absolute;
            top: 0;
            left: 0;
        }

        .slider__prev,
        .slider__next {
            position: absolute;
            bottom: 54px;
            padding: 0;

            > svg {
                width: 42px;
                height: 12px;
            }

            &:disabled {
                opacity: .3;
            }
        }

        .slider__prev {
            left: 0;
        }

        .slider__next {
            right: 0;
        }


        .slider__prev:hover > svg,
        .slider__next:hover > svg {
            opacity: 0.3;
        }

        .slider__dots {
            list-style: none;
            padding: 0;
            display: flex;
            width: auto;
            margin: 0 0 40px;
            text-align: left;
        }

        .slider__dots > li {
            position: relative;
            background-size: cover;
            width: 120px;
            height: 90px;
            margin: 0 40px 0 0;
            padding: 0;
            cursor: pointer;
            opacity: 0.45;
        }

        .slider__router-link {
            position: relative;
            display: flex;
            flex-grow: 1;
            justify-content: flex-end;
            align-items: center;
            font-family: "Helvetica", serif;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.06em;
            color: #F5F5F5;
            margin-bottom: 40px;
            padding-right: 20px;

            &::before {
                content: "";
                position: absolute;
                width: 5px;
                height: 5px;
                border-right: 1px solid #BFBFBF;
                border-bottom: 1px solid #BFBFBF;
                top: 50%;
                right: 5px;
                transform: rotate(-45deg);
                margin-top: -2px;
            }
        }

        .slider__dots > li.active {
            opacity: 1;
        }

        .slider__info {
            text-align: center;
            font-size: 14px;
            width: 100%;
            color: #F5F5F5;
        }

        .slider__name {
            margin: 17px 0 0;
        }

        .slider__number {
            line-height: 22px;
            margin: 0;
        }

        .fade-enter-active,
        .fade-leave-active {
            transition: opacity 0.5s;
        }

        .fade-enter,
        .fade-leave-to {
            opacity: 0;
        }

        .slide-enter-active,
        .slide-leave-active,
        .slide-invert-enter-active,
        .slide-invert-leave-active {
            transition: left 0.5s;
        }

        .slide-enter {
            left: 100% !important;
        }

        .slide-leave-to {
            left: -100% !important;
        }

        .slide-invert-enter {
            left: -100% !important;
        }

        .slide-invert-leave-to {
            left: 100% !important;
        }
    }
</style>
