<template>
    <nav class="main-nav">
        <transition name="links">
            <input type="text" class="main-nav__search" v-if="search">
        </transition>
        <ul class="main-nav__list">
            <li class="main-nav__item">
                <button class="main-nav__link main-nav__link--search" @click="search = !search">
                    <svg xmlns="http://www.w3.org/2000/svg" width="17.6" height="17.6" viewBox="-0.8 -0.8 17.6 17.6">
                        <path d="M16.47 14.93l-3.415-3.415A7.67 7.67 0 0 0 14.6 6.899C14.6 2.646 11.153-.8 6.9-.8S-.8 2.646-.8 6.899s3.446 7.7 7.699 7.7c1.734 0 3.326-.58 4.613-1.546l3.417 3.416c.203.202.418.33.771.33a1.1 1.1 0 0 0 1.1-1.1c0-.24-.061-.499-.33-.769zM1.399 6.899a5.5 5.5 0 1 1 11 0c0 3.038-2.462 5.5-5.5 5.5s-5.5-2.461-5.5-5.5z"/>
                    </svg>
                </button>
            </li>
            <li class="main-nav__item">
                <button class="main-nav__link main-nav__link--menu" href="#" @click="showLinks"></button>
            </li>
        </ul>
        <NavigationLinks v-bind:show="show"/>
    </nav>
</template>

<script>
    import NavigationLinks from "./NavigationLinks";

    export default {
        name: "MainNav",
        components: {NavigationLinks},
        data() {
            return {
                show: false,
                search: false
            }
        },
        methods: {
            showLinks() {
                return this.show = !this.show
            }
        }
    }
</script>

<style lang="scss">
    .main-nav {
        background-color: #202020;
        width: 100%;
        padding: 0 14px;
    }

    .main-nav__list {
        display: flex;
        justify-content: space-between;
        list-style: none;
        padding: 0;
        margin: 0;
    }

    .main-nav__link {
        width: 32px;
        height: 20px;
        background: none;
        border: none;
        cursor: pointer;

        &--menu {
            position: relative;

            &::before {
                content: "";
                position: absolute;
                width: 24px;
                height: 2px;
                top: 3px;
                left: 0;
                background: #F5F5F5;
                box-shadow: 0 7px 0 0 #F5F5F5, 0 14px 0 0 #F5F5F5;
            }
        }

        > svg {
            width: 14px;
            height: 16px;
            fill: #F5F5F5;
        }
    }

    .main-nav__search {
        width: 170px;
        height: 27px;
        font-family: Helvetica, sans-serif;
        font-size: 14px;
        color: #F5F5F5;
        background: none;
        border: none;
        border-bottom: 1px solid #F5F5F5;
        padding-right: 30px;
        margin-right: -30px;
        transition: all .3s ease-in-out;
    }

    .links-enter-active, .links-leave-active {
        transition: all .3s;
    }

    .links-enter, .links-leave-to /* .links-leave-active до версии 2.1.8 */
    {
        transform: translateX(-100%);
        opacity: 0;
    }

    @media (min-width: 1024px) {
        .main-nav {
            display: flex;
            align-items: start;
            height: fit-content;
            padding: 36px 101px 0 72px;
        }

        .main-nav__link--menu {
            display: none;
        }
    }
</style>
