<template>
    <section id="biography" class="biography container">
        <h2 class="biography__heading heading">биография</h2>
        <div class="biography__wrapper">
            <div class="biography__item" v-for="(item, index) in paragraphs" :key="index">
                <h3 class="biography__head">{{ item.year }}</h3>
                <p class="biography__text">{{ item.text }}</p>
            </div>
        </div>
        <Social v-bind:active="active" @read-more="getText"/>
    </section>
</template>

<script>
    import Social from "./Social";
    import axios from "axios";
    import _ from 'lodash';

    export default {
        name: "Biography",
        components: {Social},
        data() {
            return {
                active: false,
                items: [
                    {text: "Борис Кустодиев родился 7 марта 1878 года в Астрахани. Отца, преподавателя духовной семинарии, не стало, когда мальчику было чуть больше года. Мать осталась вдовой в 25 лет и содержала четверыхдетей."},
                    {
                        year: 1887,
                        text: "Борис сначала учился в церковно-приходской школе, потом в гимназии. Когдаему было девять лет, вгород привтать езли выставку художников-передвижников. Мальчика настолько впечатлила живопись, чтоон твердо решил научиться рисовать так же искусно. Мать нашла деньги, чтобы Борис смог брать уроки уизвестного в Астрахани художника Павла Власова."
                    },
                    {
                        year: 1896,
                        text: "Окончив семинарию, в 1896 году Кустодиев отправился учиться в Москву, но вхудожественную школу егонеприняли: Борису уже исполнилось 18 и он был слишком взрослым. Тогда Кустодиев поехал в Петербург,гдеподал документы в Высшее художественное училище при Академии художеств."
                    }
                ],
                uploadedItems: null
            }
        },
        computed: {
            paragraphs() {
                if (this.active) {
                    return _.union(this.items, this.uploadedItems);
                } else {
                    return this.items
                }
            }
        },
        methods: {
            getText() {
                if (!this.uploadedItems) {
                    axios.get('/data/Biography.json')
                        .then(response => {
                            this.uploadedItems = response.data;
                        })
                }
                this.active = !this.active;
            }
        }
    }
</script>

<style lang="scss">
    .biography {
        padding: 60px 20px;
    }

    .biography__heading {
        margin-bottom: 23px;
    }

    .biography__head {
        font-family: Helvetica, sans-serif;
        font-size: 20px;
        line-height: 40px;
        letter-spacing: 0.05em;
        color: #BFBFBF;
        margin-bottom: 5px;
    }

    .biography__text {
        font-family: Helvetica, sans-serif;
        font-size: 16px;
        line-height: 26px;
        color: #4C4C4C;
        margin: 0;
    }

    .biography__wrapper {
        margin-bottom: 19px;
    }

    @media (min-width: 1024px) {
        .biography__heading {
            width: 320px;
            margin: 0;
        }
        .biography {
            display: flex;
            flex-wrap: wrap;
            padding: 100px 0 93px;
        }

        .biography__wrapper {
            width: 600px;
            margin-bottom: 22px;
        }

        .biography__head {
            position: absolute;
            top: 0;
            left: 0;
            margin: 0;
        }

        .biography__item {
            position: relative;
            padding-left: 82px;
            margin-bottom: 23px;
        }

        .biography__text {
            letter-spacing: 0.013em;
            margin: 0;
        }
    }
</style>
